//   ? ts      
import React from 'react';
import loadable from '@loadable/component';
import { Switch, Route, Redirect } from 'react-router';

// code splitting :     
const Login = loadable(() => import('@pages/Login'));
const SignUp = loadable(() => import('@pages/SignUp'));
// const Channel = loadable(() => import('@pages/Channel')); // workspace layout  channel 
// const DirectMessage = loadable(() => import('@pages/DirectMessage')); // workspace layout  channel 
const Workspace = loadable(() => import('@layouts/Workspace'));

//  
// -  ?   Workspace  children props ,  
// -  ? Workspace     ( )   
// -   : path=""  /workspace/channel, /workspace/dm      
// - , children props   

// Route :    
// Redirect :   redirect  (react-router v5) => useNavigate(react-router v6)

// <Route />     
const App = () => {
  return (
    <Switch>
      <Redirect exact path="/" to="/login" />
      <Route path="/login" component={Login} />
      <Route path="/signup" component={SignUp} />
      {/* <Route path="/workspace/channel" component={Channel} />
      <Route path="/workspace/dm" component={DirectMessage} /> */}
      <Route path="/workspace/:workspace" component={Workspace} />
    </Switch>
  );
};

export default App;
